@import '../../sassStyles/variables';

.main-section {
    position: relative; // dont change (preloader(absolute) is depend from this SIZE + POSITION)
    width: 100%;
    height: 100%;
    background-color: $primaryBgColor;
    // padding-left: 180px; // left menu max width 180px
    .adptv {
        @extend %descr-text;
        position: absolute;
        right: 0;
        bottom: 0;
        padding: 10px;
        background-color: rgba(255,255,255, .1);
        text-transform: uppercase;
        text-align: center;
    }
}